/*
 * Base Typography
 * ---------------------------
 *
 * Loads base typography styles
 */
html {
  font-size: 20px;
}

body {
  font-size: 100%;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 80%;
  }
}

h1, h2, h3, h4, h5, h6, p, li, label, address, blockquote, dt, dd, figcaption, th, td {
  -webkit-font-smoothing: antialiased;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}

h1, h2, h3 {
  margin-top: 20px;
  margin-bottom: 10px;
}

h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 15px;
}

a {
  transition: color 400ms;
}

a:focus {
  outline: none !important;
}

p {
  margin-bottom: 20px;
}

/*
 * Content Typography
 * ---------------------------
 *
 * Styles for user generated content through a wysiwyg
 * Includes WP defined classes and TinyMCE classes
 * Should be within <main> with .main-content class
 */
.main-content blockquote {
  padding: 25px 0 25px 45px;
  border: none;
  font-size: inherit;
  font-style: italic;
}

.main-content blockquote small,
.main-content blockquote footer {
  font-size: inherit;
  font-style: normal;
  font-weight: bold;
  text-align: right;
}

@-moz-document url-prefix() {
  .main-content fieldset {
    display: table-cell;
  }
}

.main-content img {
  display: block;
  max-width: 100%;
  height: auto;
}

.main-content embed,
.main-content iframe,
.main-content object,
.main-content video {
  max-width: 100%;
}

a.active {
  color: #ffcf4c;
}

